
import Vue from 'vue'
import Vuex from 'vuex'
// import MintUI from 'mint-ui'
// import 'mint-ui/lib/style.css'
import $ from 'n-zepto'
import App from './App'
import router from './router'

// Vue.use(MintUI)
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    isLogin: false,
  },
  mutations: {
    checkLogin (state) {
      state.isLogin = true
    },
    handerQuit (state) {
      state.isLogin = false
    },
  }
})

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



