<template>
  <div class="mi">
    <div class="head">
      <p class="title"></p>
      <img :src="iconSrc" alt @click="tapIcon" />
      <p class="desc">{{nickName}}</p>
    </div>
    <ul class="list">
      <li>
        <router-link to="personalFunds">
          <img src="../assets/per_icon_zijin@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="redTotalincome">
          <img src="../assets/per_icon_hbsy@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="freetreeTotalincome">
          <img src="../assets/per_icon_cfsy@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link :to="{path:'signonSuccess', query:{ title: '', isShowBtn: false }}">
          <img src="../assets/per_icon_password@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="wechatBind">
          <img src="../assets/per_icon_code@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
      <li @click="tapItem" index="6">
        <img src="../assets/per_icon_friend@2x.png" alt />
        <p></p>
        <i></i>
      </li>
      <li>
        <router-link to="feedback">
          <img src="../assets/per_icon_feedback@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="ruleNotice">
          <img src="../assets/per_icon_notice@2x.png" alt />
          <p></p>
          <i></i>
        </router-link>
      </li>
    </ul>
    <button @click="tapQuit" v-show="isQuit"></button>
  </div>
</template>

<script>
export default {
  name: "Mi",
  data() {
    return {
      iconSrc: "../../static/per_image_default@2x.png",
      nickName: "",
      isQuit: false
    };
  },
  methods: {
    tapIcon() {
      if(this.$store.state.isLogin === false){
        this.$router.push("signin");
      }else {
        this.$router.push("personalInformation");
      }
    },
    tapQuit() {
      this.$store.commit("handerQuit");
      this.handerLogin();
    },
    handerLogin() {
      if (this.$store.state.isLogin) {
        this.iconSrc = "../../static/image_yuan@2x.png";
        this.nickName = "";
        this.isQuit = true;
      } else {
        this.iconSrc = "../../static/per_image_default@2x.png";
        this.nickName = "";
        this.isQuit = false;
      }
    },
    tapItem(e) {
      const el = e.currentTarget;
      const elIndex = el.getAttribute("index");
      const isLogin = this.$store.state.isLogin;
      if (elIndex === "6") {
        if (isLogin === false) {
          this.$router.push("signin");
        } else {
          this.$router.push("inviteFriend");
        }
      }
    }
  },
  beforeMount() {
    this.$parent.handMi();
    this.handerLogin();
  }
};
</script>

<style lang='less' scoped>
.mi {
  height: 100vh;
  background-color: rgb(238, 238, 238);
  .head {
    width: 3.75rem;
    height: 1.45rem;
    background: rgba(227, 60, 61, 1);
    padding-top: 40px;
    box-sizing: border-box;
    position: relative;
    .title {
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
    }
    img {
      width: 0.59rem;
      position: absolute;
      left: 0.15rem;
      bottom: 0.11rem;
    }
    .desc {
      font-size: 0.18rem;
      color: rgba(242, 184, 179, 1);
      line-height: 0.15rem;
      position: absolute;
      left: 0.84rem;
      bottom: 0.32rem;
    }
  }
  .list {
    li {
      width: 3.75rem;
      height: 0.46rem;
      background: rgba(255, 255, 255, 1);
      position: relative;
      img {
        width: 0.2rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.13rem;
      }
      p {
        height: 0.13rem;
        font-size: 0.14rem;
        color: rgba(63, 63, 63, 1);
        line-height: 0.15rem;
        position: absolute;
        left: 0.51rem;
        bottom: 0.17rem;
      }
      i {
        background: url("../assets/btn_more_gray@2x.png");
        background-size: contain;
        width: 0.12rem;
        height: 0.12rem;
        display: block;
        position: absolute;
        right: 0.13rem;
        bottom: 0.17rem;
      }
    }
    li:nth-child(3),
    li:nth-child(5) {
      margin-bottom: 0.1rem;
    }
  }
  button {
    font-size: 0.17rem;
    color: rgba(245, 245, 245, 1);
    width: 3.45rem;
    height: 0.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.04rem;
    margin-top: 0.2rem;
  }
}
</style>



