<template>
  <div class="personalInformation">
    <Header title=""></Header>
    <div class="list">
      <ul>
        <li @click="tapIcon">
          <p></p>
          <img src="../../../static/image_yuan@2x.png" alt />
          <i></i>
          <hr />
        </li>
        <li @click="()=>this.$router.push('setNickname')">
          <p></p>
          <span>avdcc123</span>
          <i></i>
          <hr />
        </li>
      </ul>
    </div>
    <div class="popup" v-show="isPopup">
      <div class="content">
        <p class="top"></p>
        <hr />
        <p class="bottom"></p>
      </div>
      <button @click="tapEsc"></button>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "personalInformation",
  components: {
    Header
  },
  data() {
    return {
      isPopup: false
    };
  },
  methods: {
    tapIcon() {
      this.isPopup = true;
    },
    tapEsc() {
      this.isPopup = false;
    },
  }
};
</script>

<style lang="less" scoped>
.personalInformation {
  margin-top: 0.64rem;
  .list {
    ul {
      li {
        padding: 0.1rem 0.15rem;
        display: flex;
        position: relative;
        align-items: center;
        font-size: 0.14rem;
        color: rgba(63, 63, 63, 1);
        p {
          flex: 1;
          text-align: left;
        }
        img {
          width: 0.55rem;
          margin-right: 0.1rem;
        }
        span {
          flex: 1;
          text-align: right;
          margin-right: 0.1rem;
        }
        i {
          background: url("../../../static/btn_more_gray@2x.png");
          background-size: cover;
          width: 0.12rem;
          height: 0.12rem;
        }
        hr {
          position: absolute;
          left: 0.15rem;
          top: 0.74rem;
          border: 0.5px solid #e7e7e7;
          width: 3.45rem;
          height: 0;
        }
      }
      li:nth-child(2) {
        hr {
          top: 0.46rem;
        }
      }
    }
  }
  .popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 3.75rem;
    height: 100vh;
    background-color: rgba(129, 129, 129, 0.315);
    .content {
      width: 3.45rem;
      height: 0.91rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      position: absolute;
      bottom: 0.75rem;
      left: 0.15rem;
      .top {
        font-size: 0.13rem;
        color: rgba(153, 153, 153, 1);
        line-height: 0.4rem;
      }
      hr {
        width: 3.45rem;
        border: 0.5px solid #e7e7e7;
      }
      .bottom {
        font-size: 0.18rem;
        color: rgba(218, 0, 0, 1);
        line-height: 0.5rem;
      }
    }
    button {
      width: 3.45rem;
      position: absolute;
      bottom: 0.15rem;
      left: 0.15rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      font-size: 0.17rem;
      color: rgba(218, 0, 0, 1);
      line-height: 0.45rem;
    }
  }
}
</style>


