<template>
  <div class="wechatBind">
    <Header title=""></Header>
    <div class="contain">
      <div class="title"></div>
      <hr />
      <div class="subContain">
        <img src="../../../static/shangchuan_image@2x.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "WechatBind",
  data() {
    return {};
  },
  components: {
    Header
  },
  methods: {}
};
</script>

<style lang='less' scoped>
.wechatBind {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  overflow: hidden;
  .contain {
    margin-top: 0.64rem;
    background-color: #fff;
    .title {
      height: 0.55rem;
      color: rgba(34, 34, 34, 1);
      line-height: 0.55rem;
      text-align: left;
      padding-left: 0.15rem;
    }
    hr {
      border: 0.5px solid #e7e7e7;
      margin: 0;
    }
    .subContain {
      padding: 0.35rem 0 0.35rem 0;
      img {
        width: 1.4rem;
      }
    }
  }
}
</style>



