<template>
  <div class="groupMembers">
    <div class="title">
      <img src="../assets/btn_back_white@2x.png" alt onClick='window.history.back()'/>
      <p>(55)</p>
    </div>
    <div class="content">
      <ul>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <p></p>
        </li>
      </ul>
      <div class="more">
        <button></button>
        <img src="../assets/btn_more_gray@2x.png" alt />
      </div>
    </div>
    <div class="list">
      <ul>
        <li>
          <p></p>
          <p>xxx</p>
          <img src="../assets/btn_more_gray@2x.png" alt />
        </li>
        <li class="hr"></li>
        <li>
          <p></p>
          <img src="../assets/btn_more_gray@2x.png" alt />
        </li>
        <li class="hr"></li>
        <li>
          <p></p>
          <img src="../assets/btn_more_gray@2x.png" alt />
        </li>
        <li class="mse">
          <p></p>
          <div :class="{switch: active, noSwitch: !active}" @click="handerMse">
            <i></i>
          </div>
        </li>
        <li class="hr"></li>
        <li class="mse chat">
          <p></p>
          <div :class="{switch: !chatActive, noSwitch: chatActive}" @click="handerChat">
            <i></i>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "GroupMembers",
  data() {
    return {
      active: true,
      chatActive: true,
    };
  },
  methods: {
    handerMse() {
      if (this.active) {
        this.active = false;
      } else {
        this.active = true;
      }
    },
    handerChat() {
      if (this.chatActive) {
        this.chatActive = false;
      } else {
        this.chatActive = true;
      }
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.groupMembers {
  height: 100vh;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  display: flex;
  flex-direction: column;
  .title {
    padding-top: 40px;
    width: 3.75rem;
    height: 0.64rem;
    background: rgba(57, 58, 62, 1);
    position: relative;
    img {
      width: 0.16rem;
      position: absolute;
      left: 0.17rem;
      bottom: 0.14rem;
    }
    p {
      height: 0.16rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
      margin-top: 0.35rem;
    }
  }
  .content {
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        width: 0.75rem;
        height: 0.7rem;
        img {
          margin-top: 0.1rem;
          width: 0.4rem;
          border-radius: 0.04rem;
        }
        p {
          height: 0.11rem;
          font-size: 0.12rem;
          color: rgba(68, 68, 68, 1);
          line-height: 0.15rem;
        }
      }
    }
    .more {
      margin-top: 0.1rem;
      button {
        height: 0.13rem;
        font-size: 0.14rem;
        line-height: 0.15rem;
        color: #898989;
      }
      img {
        height: 0.13rem;
        transform: translateY(0.02rem);
      }
    }
  }
  .list {
    margin-top: 0.2rem;
    ul {
      li {
        height: 0.5rem;
        position: relative;
        background-color: #fff;
        p {
          position: absolute;
          left: 0.16rem;
          top: 0.18rem;
          height: 0.14rem;
          font-size: 0.15rem;
          color: rgba(18, 18, 18, 1);
          line-height: 0.15rem;
        }
        p:nth-child(2) {
          left: 2.91rem;
          top: 0.19rem;
          height: 0.13rem;
          font-size: 0.14rem;
          color: rgba(114, 114, 114, 1);
          line-height: 0.15rem;
        }
        img {
          position: absolute;
          left: 3.49rem;
          top: 0.2rem;
          height: 0.13rem;
        }
        .switch {
          position: absolute;
          left: 3.2rem;
          top: 0.16rem;
          width: 0.4rem;
          height: 0.18rem;
          background: rgba(80, 209, 89, 1);
          border-radius: 0.09rem;
          i {
            position: absolute;
            left: 0.24rem;
            top: 0.015rem;
            width: 0.15rem;
            height: 0.15rem;
            background: rgba(255, 255, 255, 1);
            border-radius: 50%;
          }
        }
        .noSwitch {
          position: absolute;
          left: 3.2rem;
          top: 0.16rem;
          width: 0.4rem;
          height: 0.18rem;
          background: rgb(255, 255, 255);
          box-shadow: 0rem 0rem 0.03rem 0.01rem rgba(0, 0, 0, 0.22);
          border-radius: 0.09rem;
          i {
            position: absolute;
            left: 0;
            top: 0.015rem;
            width: 0.15rem;
            height: 0.15rem;
            background: rgba(255, 255, 255, 1);
            box-shadow: 0rem 0rem 0.03rem 0.01rem rgba(0, 0, 0, 0.22);
            border-radius: 50%;
          }
        }
      }
      .hr {
        width: 3.45rem;
        margin-left: 0.15rem;
        height: 1px;
        background: rgba(247, 247, 247, 1);
      }
      .mse {
        margin-top: 0.1rem;
      }
      .chat{
        margin-top: 0;
      }
    }
  }
}
</style>



