<template>
  <div class="inviteFriend">
    <Header title="" class="component-header"></Header>
    <div class="header">
      <img src="../../../static/yqoing_bg@2x.png" alt />
    </div>
    <div class="activeRule">
      <img src="../../../static/huodong_image@2x.png" alt />
      <p>6%5%4%</p>
    </div>
    <div class="main">
      <p class="title"></p>
      <p class="code">WHK123456</p>
      <button></button>
      <hr />
      <img src="../../../static/erweima_image@2x.png" alt />
      <p class="bottom"></p>
    </div>
  </div>
</template>

<script>
import Header from '../customize/Header'
export default {
  name: "InviteFriend",
  data() {
    return {};
  },
  components:{
    Header,
  },
  methods: {}
};
</script>

<style lang='less' scoped>
.inviteFriend {
  background-color: #ffeee6;
  .component-header{
    background-color: transparent;
  }
  .header {
    img {
      width: 3.75rem;
    }
  }
  .activeRule {
    width: 3.12rem;
    height: 1.5rem;
    background-color: #fff;
    margin: auto;
    transform: translateY(-0.36rem);
    overflow: hidden;
    img {
      width: 1.16rem;
      margin-top: 0.26rem;
    }
    p {
      font-size: 0.12rem;
      font-family: SimHei;
      color: rgba(108, 71, 47, 1);
      line-height: 0.15rem;
      text-align: left;
      padding: 0.16rem 0.15rem 0 0.15rem;
    }
  }
  .main {
    width: 3.12rem;
    background: rgba(255, 255, 255, 1);
    margin: auto;
    transform: translateY(-0.2rem);
    overflow: hidden;
    .title {
      font-size: 0.15rem;
      font-weight: bold;
      margin-top: 0.25rem;
    }
    .code {
      font-size: 0.14rem;
      font-weight: bold;
      color: rgba(227, 60, 61, 1);
      margin-top: 0.15rem;
    }
    button {
      width: 0.63rem;
      line-height: 0.2rem;
      background: rgba(227, 60, 61, 1);
      border-radius: 0.02rem;
      margin-top: 0.1rem;
      font-size: 0.13rem;
      color: rgba(255, 255, 255, 1);
    }
    hr {
      width: 2.3rem;
      border: 0.5px dashed rgba(160, 160, 160, 1);
      margin: 0.2rem auto;
    }
    img {
      width: 0.74rem;
    }
    .bottom {
      font-size: 0.11rem;
      margin-top: 0.07rem;
      margin-bottom: 0.2rem;
      color: rgba(10, 0, 0, 1);
      opacity: 0.7;
    }
  }
}
</style>



