<template>
  <div class="signin">
    <img src="../assets/app_cancel_icon@2x.png" alt class="closs" @click='()=>this.$router.push("mi")' />
    <img src="../assets/home_logo@2x.png" alt class="logo" />
    <p class="title">EOSATM</p>
    <p class="youqing"></p>
    <ul class="list">
      <li>
        <img src="../assets/login_input_icon@2x.png" alt />
        <input type="text" placeholder="" />
      </li>
    </ul>
    <button @click="tapRegister"></button>
  </div>
</template>

<script>
export default {
  name: "Signin",
  data() {
    return {
      msg: "1111111111111111111"
    };
  },
  methods:{
    tapRegister(){
      this.$router.push({path:'signonSuccess',query:{ title: '', isShowBtn: true }})
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.signin {
  height: 100vh;
  background-color: rgb(243, 243, 243);
  position: relative;
  .closs {
    width: 0.16rem;
    position: absolute;
    top: 0.34rem;
    right: 0.15rem;
  }
  .logo {
    width: 0.87rem;
    position: absolute;
    top: 0.66rem;
    right: 1.44rem;
  }
  .title {
    width: 1.27rem;
    height: 0.24rem;
    font-size: 0.31rem;
    font-family: Noto Sans S Chinese;
    font-weight: bold;
    color: rgba(0, 9, 18, 1);
    line-height: 0.15rem;
    position: absolute;
    top: 1.7rem;
    right: 1.24rem;
  }
  .youqing {
    height: 0.17rem;
    font-size: 0.18rem;
    font-weight: 500;
    color: rgba(227, 60, 61, 1);
    line-height: 0.15rem;
    position: absolute;
    top: 2.88rem;
    right: 1.61rem;
  }
  .list {
    li {
      width: 3.45rem;
      height: 0.45rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.05rem;
      position: absolute;
      top: 3.25rem;
      right: 0.15rem;
      img {
        width: 0.16rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.13rem;
      }
      input {
        height: 0.14rem;
        font-size: 0.15rem;
        color: rgba(181, 184, 199, 1);
        line-height: 0.15rem;
        position: absolute;
        left: 0.45rem;
        bottom: 0.13rem;
        border: none;
      }
    }
  }
  button {
    width: 3.45rem;
    height: 0.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.05rem;
    position: absolute;
    top: 3.95rem;
    right: 0.15rem;
  }
}
</style>



