        p:nth-child(4) {
          height: 0.11rem;
          font-size: 0.11rem;
          color: rgba(64, 64, 64, 1);
          line-height: 0.08rem;
          margin-top: 0.06rem;
        }
      }
    }
    li:nth-child(3n){
      margin-right: 0;
    }
  }
}
</style>



