<template>
  <div class="signonSuccess">
    <div class="header">
      <div class="title">
        <i onClick='window.history.back()'></i>
        <p>{{title}}</p>
      </div>
    </div>
    <div class="popup">
      <p class="tip"></p>
      <div class="main">
        <p class="top"></p>
        <p class="bottom">12321252</p>
        <button></button>
      </div>
      <hr style="border: 0.5px dashed rgba(160,160,160,1); width:2.3rem;" />
      <div class="main">
        <p class="top"></p>
        <p class="bottom">525562</p>
        <button></button>
      </div>
    </div>
    <button class="verify" @click="()=>this.$router.push('signonSuccessNext')" v-show="isShowBtn"></button>
  </div>
</template>

<script>
export default {
  name: "SignonSuccess",
  data() {
    return {
      isShowBtn: true,
      title: '',
    };
  },
  methods: {},
  beforeMount(){
    this.title = this.$route.query.title
    this.isShowBtn = this.$route.query.isShowBtn
  }
  
};
</script>

<style lang='less' scoped>
.signonSuccess {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  .header {
    width: 3.75rem;
    height: 0.64rem;
    background: rgba(57, 58, 62, 1);
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        margin-top: 0.3rem;
      }
      i {
        background: url("../../../static/btn_back_white@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.2rem;
        top: 0.34rem;
      }
    }
  }
  .popup {
    width: 3.45rem;
    background: rgba(255, 255, 255, 1);
    border-radius: 0.03rem;
    margin-top: 0.15rem;
    margin-left: 0.15rem;
    overflow: hidden;
    .tip {
      width: 3.2rem;
      margin-top: 0.28rem;
      margin-left: 0.15rem;
      font-size: 0.15rem;
      color: rgba(64, 64, 64, 1);
      text-align: left;
    }
    .main {
      margin-top: 0.2rem;
      margin-bottom: 0.2rem;
      .top {
        font-size: 0.15rem;
        color: rgba(64, 64, 64, 1);
      }
      .bottom {
        font-size: 0.14rem;
        color: rgba(227, 60, 61, 1);
        margin-top: 0.1rem;
        margin-bottom: 0.1rem;
      }
      button {
        font-size: 0.13rem;
        color: rgba(255, 255, 255, 1);
        width: 0.63rem;
        line-height: 0.2rem;
        background: rgba(227, 60, 61, 1);
        border-radius: 0.03rem;
      }
    }
  }
  .verify {
    font-size: 0.17rem;
    color: rgba(245, 245, 245, 1);
    line-height: 0.45rem;
    width: 3.45rem;
    margin-top: 0.2rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
  }
}
</style>



